@import "../components/mixins";
@import "../components/variables";

body {
  background-color: $semi-dark;
  background-color: red;
  color: rgba(255, 255, 255, 0.5);
  font-weight: 600;
}
nav {
  background-color: $dark;
  height: 100vh;
  width: 280px;
  position: fixed;
  transition: all 0.3s ease;
  overflow: auto;
  .nav-item {
    transition: all 0.3s ease;
    text-decoration: none;
    color: rgba(#FFFFFF, 0.6);
    &:hover {
      color: rgba(#FFFFFF, 0.8);
      margin-left: 0.6rem;
    }
  }
  .category-title {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.4);
  }
}
main {
  min-height: 100vh;
  transition: all 0.3s ease; margin-left: 300px;
}

.collapsed {
  nav {
    width: 80px;
    .text{
      display: none;
    }
    .category {
      display: none;
    }
    &:hover {
      width: 300px;
      .text{
        display: inline;
      }
      .category {
        display: inline;
      }
    }
  }
  main {
    margin-left: 100px;
  }
}
